import React, { Component, Suspense } from "react";
import { connect } from "react-redux";
import {
  getScreenAnalyticsData,
} from "../../../../../../actions/analyticsAction";
import { Card, CardBody } from "react-bootstrap";
import ImageSkeleton from "../../layout/skeleton/imageSkeleton";
import SectionHeading from "../../layout/common/sectionHeading";

const TopCard = React.lazy(() =>
  import("../../layout/common/topCard")
)
const Tabheader = React.lazy(() =>
  import("../../layout/common/subheader/tabHeader")
)
const AssetsTypeAreaChart = React.lazy(() =>
  import("../../layout/common/graph/assetsTypeAreaChart")
);
const ScreensOverview = React.lazy(() =>
  import("./components/screensOverview")
);

class Display extends Component {  
  componentDidMount() {
    const { getScreenAnalytics, analyticsStatus, selectedWallFeed  } = this.props;
      getScreenAnalytics(!selectedWallFeed?.length ? true : analyticsStatus)
    }

  render() {
    const {
      get_screen_overview_data,
      get_screen_overview_data_loader,
      get_screen_analytics_data,
      get_screen_analytics_loader
    } = this.props.analyticsRecords;


    return (
      <>
            <div className="">
              <Suspense fallback={<></>}>
                <Tabheader />
               </Suspense>  
              <div className="row">
                <Suspense fallback={<></>}>
                  <TopCard loader={get_screen_analytics_loader} data={get_screen_analytics_data} col={2} res={6}  />
                </Suspense>  
              </div>

              <Suspense fallback={<></>}>
                <ScreensOverview
                  loader={get_screen_overview_data_loader}
                  {...get_screen_overview_data}
                />
              </Suspense>

              <Card className="sw-card-custom gutter-b p-3 card-shadow">
                <SectionHeading  section={3}  title={'Content Analytics'} />
                <CardBody className="p-0">
                  { get_screen_overview_data_loader ? (<ImageSkeleton img={3} />) : (
                    <Suspense fallback={<></>}>
                        <AssetsTypeAreaChart
                        key={get_screen_overview_data_loader}
                        get_screen_overview_data={get_screen_overview_data}
                        type={"bar"}
                      />
                    </Suspense>
                  )}
                </CardBody>
              </Card>
            </div>
      </>
    );
  }
}


const widgetstateProps = (state) => {  
  return {
    analyticsRecords: state.analyticsRecords,
    selectedWallFeed: state.analyticwall?.selectedWallFeed?.feeds,
    detail: state.appsMore.detail,
    analyticsStatus : state.analyticsStatus.trialAccessAPI
  };
};

const widgetDispatchProps = (dispatch) => {
  return {
    getScreenAnalytics: (isTrial) => dispatch(getScreenAnalyticsData(isTrial)),
  };
};

export default connect(widgetstateProps, widgetDispatchProps)(Display);
